/* Website */
@media screen and (min-device-width: 768px) and (max-device-width: 2500px) {
  @import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");
 
  :root {
    scroll-behavior: smooth !important;
  }

  body {
    font-family: "Inter", sans-serif !important;
    color: #444444;
    overflow-x: hidden;
    background-color: #f6f7fa !important;
    animation: fadeInAnimation ease 2.5s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
  }

  @keyframes fadeInAnimation {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  a {
    color: #3e4093;
    text-decoration: none;
    cursor: pointer !important;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    font-family: "Poppins";
  }
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  @keyframes fadeinout {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  /*--------------------------------------------------------------
    # Header
    --------------------------------------------------------------*/
  .comman_btn_search {
    background-color: #3e4093;
    color: #fff;
    padding: 2px 6px;
    transition: 0.5s all;
    border-radius: 6px 0px 0px 6px;
    position: relative;
    left: 4px;
    z-index: 9;
  }
  .searchDrop {
    background-color: #3e4093;
    border: none;
  }
  
  /* welcome screeen */
  .welcome {
    font-family: "Open Sans", sans-serif;
    color: #fff;
    background: url("../img/bg.jpg") top center no-repeat;
    background-size: cover;
    position: relative;
    height: 100vh;
  }
  .welcome::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(34, 34, 34, 0.8);
  }
  @media (min-width: 1024px) {
    .welcome {
      background-attachment: fixed;
    }
  }

  .welcome a {
    color: #ff0000;
    text-decoration: none;
  }

  .welcome a:hover {
    color: #ff3333;
    text-decoration: none;
  }

  .welcome h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: "Raleway", sans-serif;
  }

  #main {
    position: relative;
  }

  /*--------------------------------------------------------------
  # Back to top button
  --------------------------------------------------------------*/
  .back-to-top {
    position: fixed;
    visibility: hidden;
    opacity: 0;
    right: 15px;
    bottom: 15px;
    z-index: 996;
    background: #ff0000;
    width: 40px;
    height: 40px;
    border-radius: 4px;
    transition: all 0.4s;
  }

  .back-to-top i {
    font-size: 28px;
    color: #fff;
    line-height: 0;
  }

  .back-to-top:hover {
    background: #ff2929;
    color: #fff;
  }

  .back-to-top.active {
    visibility: visible;
    opacity: 1;
  }

  /*--------------------------------------------------------------
  # Header
  --------------------------------------------------------------*/
  #header {
    position: relative;
    width: 100%;
    padding: 50px 0;
    /* countdown */
  }

  #header h1 {
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    line-height: 56px;
    color: #fff;
  }

  #header h2 {
    color: #eee;
    margin-bottom: 40px;
    font-size: 22px;
  }

  #header .countdown {
    margin-bottom: 0px;
  }

  #header .countdown div {
    text-align: center;
    margin: 10px;
    width: 150px;
    padding: 15px 0;
    background: rgba(255, 255, 255, 0.12);
    border-top: 5px solid #eb3237;
  }

  #header .countdown div h3 {
    font-weight: 700;
    font-size: 44px;
    margin-bottom: 15px;
  }

  #header .countdown div h4 {
    font-size: 16px;
    font-weight: 600;
  }

  @media (max-width: 575px) {
    #header .countdown div {
      width: 70px;
      padding: 10px 0;
      margin: 10px 8px;
    }

    #header .countdown div h3 {
      font-size: 28px;
      margin-bottom: 10px;
    }

    #header .countdown div h4 {
      font-size: 14px;
      font-weight: 500;
    }
  }

  #header .subscribe {
    font-size: 15px;
    text-align: center;
  }

  #header .subscribe h4 {
    font-size: 20px;
    font-weight: 600;
    color: #fff;
    position: relative;
    padding-bottom: 12px;
  }

  #header .subscribe .subscribe-form {
    min-width: 300px;
    margin-top: 10px;
    background: #fff;
    padding: 6px 10px;
    position: relative;
    text-align: left;
  }

  #header .subscribe .subscribe-form input[type="email"] {
    border: 0;
    padding: 4px 8px;
    width: calc(100% - 100px);
  }

  #header .subscribe .subscribe-form input[type="submit"] {
    position: absolute;
    top: 0;
    right: -2px;
    bottom: 0;
    border: 0;
    background: none;
    font-size: 16px;
    padding: 0 20px;
    background: #ff0000;
    color: #fff;
    transition: 0.3s;
    box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
  }

  #header .subscribe .subscribe-form input[type="submit"]:hover {
    background: #f50000;
  }

  #header .subscribe .error-message {
    display: none;
    color: #ed3c0d;
    text-align: center;
    padding: 15px;
    font-weight: 600;
  }

  #header .subscribe .sent-message {
    display: none;
    color: #18d26e;
    text-align: center;
    padding: 15px;
    font-weight: 600;
  }

  #header .subscribe .loading {
    display: none;
    text-align: center;
    padding: 15px;
  }

  #header .subscribe .loading:before {
    content: "";
    display: inline-block;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    margin: 0 10px -6px 0;
    border: 3px solid #18d26e;
    border-top-color: #eee;
    -webkit-animation: animate-loading-notify 1s linear infinite;
    animation: animate-loading-notify 1s linear infinite;
  }

  @-webkit-keyframes animate-loading-notify {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  @keyframes animate-loading-notify {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  #header .social-links {
    margin-top: 10px;
  }

  #header .social-links a {
    font-size: 24px;
    display: inline-block;
    color: rgba(255, 255, 255, 0.8);
    line-height: 1;
    padding-top: 14px;
    margin: 0 10px;
    text-align: center;
    transition: 0.3s;
  }

  #header .social-links a:hover {
    color: #fff;
  }

  /*--------------------------------------------------------------
  # Sections General
  --------------------------------------------------------------*/
  .welcome section {
    padding: 60px 0;
  }

  .section-bg {
    background-color: #fff5f5;
  }

  .section-title {
    text-align: center;
    padding-bottom: 15px;
  }

  .section-title h2 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    position: relative;
  }

  .section-title p {
    margin-bottom: 0;
  }
  .marginTop {
    margin-top: 0rem !important;
  }
  .marginTopLog {
    margin-top: 0rem !important;
  }
  .photos {
    margin-top: 8.6rem;
    background-color: #f1f0ff;
  }
  .photos_main {
    padding: 1rem;

    border-radius: 0.2rem;
  }
  .photos_main img {
    max-width: 26rem;
    min-width: 20rem;
    min-height: 18rem;
    max-height: 20rem;
    margin-bottom: 0.8rem !important;
    border-radius: 5px;
  }
  .photos_main img:hover {
    opacity: 50%;
    filter: brightness(70%);
    cursor: zoom-in;
  }
  @media (max-width: 800px) {
    .photos_main img {
      max-width: 26rem;
      min-width: 20rem;
      margin: 1rem;
    }
  }

  /*--------------------------------------------------------------
  # Contact Us
  --------------------------------------------------------------*/
  .about .content h2 {
    font-weight: 700;
    font-size: 48px;
    line-height: 60px;
    margin-bottom: 20px;
    text-transform: uppercase;
  }

  .about .content h3 {
    font-weight: 500;
    line-height: 32px;
    font-size: 24px;
  }

  .about .content ul {
    list-style: none;
    padding: 0;
  }

  .about .content ul li {
    padding: 10px 0 0 28px;
    position: relative;
  }

  .about .content ul i {
    left: 0;
    top: 12px;
    position: absolute;
    font-size: 20px;
    color: #ff0000;
  }

  .about .content p:last-child {
    margin-bottom: 0;
  }

  /*--------------------------------------------------------------
  # Contact Us
  --------------------------------------------------------------*/
  .contact .info-wrap {
    background: rgba(255, 255, 255, 0.06);
    padding: 30px;
  }

  .contact .info i {
    font-size: 20px;
    color: #fff;
    float: left;
    width: 44px;
    height: 44px;
    background: rgba(255, 255, 255, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    transition: all 0.3s ease-in-out;
  }

  .contact .info h4 {
    padding: 0 0 0 60px;
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 5px;
  }

  .contact .info p {
    padding: 0 0 10px 60px;
    margin-bottom: 20px;
    font-size: 14px;
  }

  .contact .info .email p {
    padding-top: 5px;
  }

  .contact .info .social-links {
    padding-left: 60px;
  }

  .contact .info .social-links a {
    font-size: 18px;
    display: inline-block;
    background: #333;
    color: #fff;
    line-height: 1;
    padding: 8px 0;
    border-radius: 50%;
    text-align: center;
    width: 36px;
    height: 36px;
    transition: 0.3s;
    margin-right: 10px;
  }

  .contact .info .social-links a:hover {
    background: #ff0000;
    color: #fff;
  }

  .contact .info .email:hover i,
  .contact .info .address:hover i,
  .contact .info .phone:hover i {
    background: #ff0000;
    color: #fff;
  }

  .contact .php-email-form {
    width: 100%;
    padding: 30px;
    padding-top: 0;
    background: rgba(255, 255, 255, 0.06);
  }

  .contact .php-email-form .form-group {
    padding-bottom: 8px;
  }

  .contact .php-email-form .validate {
    display: none;
    color: red;
    margin: 0 0 15px 0;
    font-weight: 400;
    font-size: 13px;
  }

  .contact .php-email-form .error-message {
    display: none;
    color: #fff;
    background: #ed3c0d;
    text-align: center;
    padding: 15px;
    font-weight: 600;
  }

  .contact .php-email-form .sent-message {
    display: none;
    color: #fff;
    background: #18d26e;
    text-align: center;
    padding: 15px;
    font-weight: 600;
  }

  .contact .php-email-form .loading {
    display: none;
    background: #fff;
    text-align: center;
    padding: 15px;
  }
  .viewAllBtn {
    font-size: 18px;
    font-weight: bold;
    color: #989797;
    cursor: pointer;
    position: relative;
    left: 46.5%;
    bottom: 33px;
  }
  .viewAllBtn:hover {
    color: #eb3237;
  }
  .contact .php-email-form .loading:before {
    content: "";
    display: inline-block;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    margin: 0 10px -6px 0;
    border: 3px solid #18d26e;
    border-top-color: #eee;
    -webkit-animation: animate-loading 1s linear infinite;
    animation: animate-loading 1s linear infinite;
  }

  .contact .php-email-form input,
  .contact .php-email-form textarea {
    border-radius: 0;
    box-shadow: none;
    font-size: 14px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: #fff;
  }

  .contact .php-email-form input::-moz-placeholder,
  .contact .php-email-form textarea::-moz-placeholder {
    color: rgba(255, 255, 255, 0.4);
  }

  .contact .php-email-form input::placeholder,
  .contact .php-email-form textarea::placeholder {
    color: rgba(255, 255, 255, 0.4);
  }

  .contact .php-email-form input {
    height: 44px;
  }

  .contact .php-email-form textarea {
    padding: 10px 12px;
  }

  .contact .php-email-form button[type="submit"] {
    background: #ff0000;
    border: 0;
    padding: 10px 24px;
    color: #fff;
    transition: 0.4s;
  }

  .contact .php-email-form button[type="submit"]:hover {
    background: #ff3333;
  }

  @-webkit-keyframes animate-loading {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  @keyframes animate-loading {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  /*--------------------------------------------------------------
  # Footer
  --------------------------------------------------------------*/
  #footer {
    background: rgba(19, 19, 19, 0.4);
    padding: 30px 0;
    color: #fff;
    font-size: 14px;
    position: relative;
    margin-top: 35px;
  }

  #footer .copyright {
    text-align: center;
  }

  #footer .credits {
    padding-top: 10px;
    text-align: center;
    font-size: 13px;
    color: #fff;
  }

  #footer .credits a {
    color: #ff3333;
  }
  .navbar-brand img {
    max-width: 150px;
    margin: 10px 10px;
  }
  .navbar-brand {
    margin: 15px 0px 0px 20px;
  }
  /* welcome end */
  .comman_btn2 {
    background-color: #3e4093 !important;
    padding: 12px 28px !important;
    text-decoration: none !important;
    font-size: 14px !important;
    font-weight: 600;
    transition: 0.5s all;
    color: #fff;
    border-radius: 60px !important;
    border: 0;
    display: inline-flex;
    cursor: pointer !important;
  }
  .comman_btn2:hover {
    background-color: #eb3237 !important;
    color: #fff !important;
  }

  .header_bottom {
    padding-left: 40px;
    padding-bottom: 8px !important;
    position: relative;
    display: flex;
    font-weight: 600;
    font-size: 0.6rem;
    justify-content: center !important;
    background-color: #3e4093;
    height: 35px !important;
  }
  .container-fluid {
    max-width: 96%;
  }
  .comman_head h2 {
    font-weight: 700;
    font-size: 36px;
    text-transform: uppercase;
  }
  .header_menus {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }


  .header_logo img {
    max-width: 150px;
  }

  .signup_btns {
    background-color: #3e4093;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 10px 19px;
    color: #fff !important;
    font-size: 12px;
    font-weight: 500;
    border-radius: 50px;
    transition: 0.5s all;
  }
  .signup_btns:hover {
    background-color: #eb3237;
  }
  .login_btns {
    color: #3e4093;
    font-weight: 600;
    font-size: 15px;
    display: inline-block;
    margin: 0 15px;
    text-decoration: none;
  }
  .fade-scale {
    transform: scale(0);
    opacity: 0;
    -webkit-transition: all 0.25s linear;
    -o-transition: all 0.25s linear;
    transition: all 0.25s linear;
  }
  .fade-scale.in {
    opacity: 1;
    transform: scale(1);
  }
  .social_icon a {
    font-size: 19px;
    display: inline-block;
    margin-right: 18px;
    color: #3e4093;
    position: relative;
  }
  .count {
    position: absolute;
    top: -11px !important;
    right: -5px;
    display: flex;
    background-color: #eb3237;
    height: 10px;
    width: 12px;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    color: #fff;
    font-weight: 500;
    font-size: 12px;
  }
  .social_icon {
    border-right: 2px solid #3e4093;
  }
  .header_search .form-control {
    height: 34px;
    font-size: 13px;
    padding: 0 20px;
    border: 0;
  }
  .form-New-select {
    height: 35px !important;
    font-size: 13px;
    padding: 0 20px;
    border-radius: 6px;
    border: 2px grey !important;
  }
  .header_search {
    border-radius: 4px;
    overflow: hidden;
    height: 38px;
    display: inline-block;
    border: 2px solid #3e4093;
    width: 100%;
    position: relative;
  }
  .header_search .Btn-design {
    background-color: #3e4093;
    height: 40px !important;
    width: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    border: 0;
    margin-right: -1px;
    font-size: 13px;
  }
  .header_menus li a {
    color: #fff;
    font-size: 15px;
    text-transform: capitalize;
    display: flex;
    align-items: center;
    position: relative;
  }
  .header_menus li {
    list-style: none;
    margin-left: 18px;
    position: static;
  }
  .header_top {
    background-color: #eef3ff;
  }
  a:hover {
    color: #eb3237;
  }

  .forms_modal .modal-body {
    padding: 25px;
  }
  .btn-close {
    position: absolute;
    right: 10px;
    font-size: 12px;
    opacity: 1;
    top: 10px;
  }
  .heading_forms h3 {
    font-size: 22px;
    font-weight: 700;
    margin: 0 0 3px;
  }
  .heading_forms span {
    display: block;
    font-size: 14px;
    color: #9f9d9d;
  }
  .forms_modal_content .form-group label {
    font-weight: 500;
