    font-size: 22px;
    font-weight: 700;
    margin: 0 0 15px;
  }
  .comman_modal_heading p {
    font-size: 14px;
    margin-bottom: 25px;
  }
  .time_js span {
    font-weight: 600;
    color: #3e4093;
    font-size: 20px;
    display: block;
  }
  .otp_part .form-group .form-control {
    width: 46px;
