    padding: 0;
    text-align: center;
    font-size: 14px;
  }
  .comman_modal_form .modal-dialog {
    max-width: 400px;
  }
  .banner_box {
    margin-top: 9rem;
  }
  .banner_slide {
    height: calc(100vh - 153px);
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    align-items: center;
    background-repeat: no-repeat;
    background-color: #000;
    opacity: 100%;
  }
  .banner_slides::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    background-color: #000;
    opacity: 0.9;
  }
  .category_btn {
    background-color: #f20;
    color: #fff;
    font-weight: 600;
    font-size: 14px;
    height: 120px;
    width: 120px;
    border-radius: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
  .banner_content {
    position: relative;
    z-index: 1;
  }
  .banner_content h2 {
    color: #fff;
    font-weight: 800;
    font-size: 46px;
    text-transform: capitalize;
    margin-bottom: 20px;
  }
  .banner_content p {
    font-weight: 400;
    color: #fff;
    font-size: 16px;
    line-height: 28px;
  }
  .banner_sider .owl-nav button {
    background-color: #eb3237 !important;
    color: #fff !important;
    height: 50px;
    width: 50px;
    border-radius: 50px;
    font-size: 30px !important;
  }
  .banner_sider .owl-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    width: 95%;
    top: 50%;
    transform: translateY(-50%);
    margin: 0 auto;
    left: 0;
    right: 0;
  }
  .flavour_box_img {
    background-color: #000 !important;
  }
  section {
    overflow: hidden;
