  .cardTp {
    --font-color: #323232;
    --font-color-sub: #666;
    --bg-color: #fff;
    --main-color: #323232;
    --main-focus: #2d8cf0;
    /* width: 230px; */
    max-height: 16rem !important;
    background: var(--bg-color);
    border: 2px solid var(--main-color);
    box-shadow: 4px 4px var(--main-color);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 20px;
    gap: 10px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
  }

  .cardTp:last-child {
    justify-content: flex-end;
  }

  .cardTp-img {
    /* clear and add new css */
    transition: all 0.5s;
    display: flex;
    justify-content: center;
  }

  .cardTp-img .img {
    /* delete */
    transform: scale(1);
    position: relative;
    box-sizing: border-box;
    width: 100px;
    height: 100px;
    background-color: #228b22;
    background-image: linear-gradient(
      to top,
      transparent 10px,
      rgba(0, 0, 0, 0.3) 10px,
      rgba(0, 0, 0, 0.3) 13px,
      transparent 13px
    );
  }

  .cardTp-img .img::before {
    /* delete */
    content: "";
    position: absolute;
    width: 65px;
    height: 110px;
    margin-left: -32.5px;
    left: 50%;
    bottom: -4px;
    background-repeat: no-repeat;
    background-image: radial-gradient(
        ellipse at center,
        rgba(0, 0, 0, 0.7) 30%,
        transparent 30%
      ),
      linear-gradient(
        to top,
        transparent 17px,
        rgba(0, 0, 0, 0.3) 17px,
        rgba(0, 0, 0, 0.3) 20px,
        transparent 20px
      ),
      linear-gradient(to right, black 2px, transparent 2px),
      linear-gradient(to left, black 2px, transparent 2px),
      linear-gradient(to top, black 2px, #228b22 2px);
    background-size: 60% 10%, 100% 100%, 100% 65%, 100% 65%, 100% 50%;
    background-position: center 3px, center bottom, center bottom, center bottom,
      center bottom;
    border-radius: 0 0 4px 4px;
    z-index: 2;
  }

  .cardTp-img .img::after {
    /* delete */
    content: "";
    position: absolute;
    box-sizing: border-box;
    width: 25px;
    height: 25px;
    margin-left: -14px;
    left: 50%;
    top: -13px;
    background-repeat: no-repeat;
    background-image: linear-gradient(80deg, #ffc0cb 45%, transparent 45%),
      linear-gradient(-175deg, #ffc0cb 45%, transparent 45%),
      linear-gradient(80deg, rgba(0, 0, 0, 0.2) 51%, rgba(0, 0, 0, 0) 51%),
      linear-gradient(-175deg, rgba(0, 0, 0, 0.2) 51%, rgba(0, 0, 0, 0) 51%),
      radial-gradient(
        circle at center,
        #ffa6b6 45%,
        rgba(0, 0, 0, 0.2) 45%,
        rgba(0, 0, 0, 0.2) 52%,
        rgba(0, 0, 0, 0) 52%
      ),
      linear-gradient(
        45deg,
        rgba(0, 0, 0, 0) 48%,
        rgba(0, 0, 0, 0.2) 48%,
        rgba(0, 0, 0, 0.2) 52%,
        rgba(0, 0, 0, 0) 52%
      ),
      linear-gradient(
        65deg,
        rgba(0, 0, 0, 0) 48%,
        rgba(0, 0, 0, 0.2) 48%,
        rgba(0, 0, 0, 0.2) 52%,
        rgba(0, 0, 0, 0) 52%
      ),
      linear-gradient(
        22deg,
        rgba(0, 0, 0, 0) 48%,
        rgba(0, 0, 0, 0.2) 48%,
        rgba(0, 0, 0, 0.2) 54%,
        rgba(0, 0, 0, 0) 54%
      );
    background-size: 100% 100%, 100% 100%, 100% 100%, 100% 100%, 100% 100%,
      100% 75%, 100% 95%, 100% 60%;
    background-position: center center;
    border-top-left-radius: 120px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 70px;
    transform: rotate(45deg);
    z-index: 1;
  }

